import React, { Component, createRef } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import logo from "../../assets/imges/logo.jpeg";
import { getCookie } from "../../utils/checkCookie";
import headerImage from "../../assets/imges/header.png";
import {
  fetchingLogin,
  fetchngError,
  fetchingHeaderName,
  fetchingUpdateRedirect,
  fetchingGetUser,
} from "../../redux/actions/actions";
import "./login.css";

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: "",
      logoWidth: "60%",
      innerWidth: 0,
      viewWidth: 0,
    };
    this.loginRef = createRef();
  }
  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleClick = () => {
    if (!this.state.username || !this.state.password) {
      this.props.fetchngError("username or password can not be empty");
    } else {
      this.props.fetchingHeaderName("Main");
      this.props.fetchingLogin({
        username: this.state.username,
        password: this.state.password,
      });
    }
  };

  callback = () => {
    if (window.visualViewport.width < 600) {
      this.setState({
        logoWidth: "100%",
      });
    } else {
      this.setState({
        logoWidth: "60%",
      });
    }
    if (this.loginRef.current) {
      if (window.visualViewport.width < 800) {
        this.loginRef.current.style.width = `${window.visualViewport.width}px`;
      } else {
        this.loginRef.current.style.width = "800px";
      }
      this.setState({
        viewWidth: this.loginRef.current.getBoundingClientRect().width,
        innerWidth: window.visualViewport.width,
      });
    }
  };

  componentDidMount() {
    this.callback();
    window.addEventListener("resize", this.callback);
    if (getCookie()) {
      this.props.fetchingGetUser(() => {});
    }
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.callback);
  }

  render() {
    if (this.props.user._id && getCookie()) {
      // return <Redirect to={this.props.user.redirect} />;
      if (!this.props.user.head) {
        this.props.fetchingUpdateRedirect("/info/complete");
        this.props.history.push("/info");
        return null;
      } else {
        this.props.history.push("/homepage");
        setTimeout(() => {
          window.location.reload();
        }, 0);
        return null;
      }
    }
    return (
      <div ref={this.loginRef} className="login-wrap background">
        <div
          className="login-header"
          style={{
            left: `${(this.state.innerWidth - this.state.viewWidth) / 2}px`,
            width: `${this.state.viewWidth}px`,
          }}
        >
          <img src={headerImage} alt="headerImage" />
        </div>
        <div className="logo">
          <img src={logo} alt="logo" style={{ width: this.state.logoWidth }} />
        </div>
        <div className="error">
          <span>{this.props.user.msg}</span>
        </div>
        <input
          className="input"
          type="text"
          placeholder="username"
          value={this.state.username}
          onChange={(e) => this.handleChange("username", e.target.value)}
        />
        <input
          className="input"
          type="password"
          placeholder="password"
          value={this.state.password}
          onChange={(e) => this.handleChange("password", e.target.value)}
        />
        <div className="login-button">
          <button className="button" onClick={this.handleClick}>
            Login
          </button>
          <button
            className="button"
            onClick={() => {
              this.props.fetchngError("");
              this.props.history.push("/register");
            }}
          >
            Register
          </button>
        </div>
      </div>
    );
  }
}

export default connect(
  (state) => ({
    user: state.user,
  }),
  {
    fetchingLogin,
    fetchngError,
    fetchingHeaderName,
    fetchingUpdateRedirect,
    fetchingGetUser,
  }
)(Login);
