import axios from "axios";

const url = "https://www.dreamgoalreality.top:4000";

export const ajax_register = async (username, password) => {
  const result = await axios.post(
    url + "/register",
    { username, password },
    { withCredentials: true }
  );
  if (result.data) {
    return result.data;
  }
};

export const ajax_update = async (updateInfo) => {
  const result = await axios.post(
    url + "/update",
    // { head, sex, prefer, detail, age },
    { ...updateInfo },
    { withCredentials: true }
  );
  if (result.data) {
    return result.data;
  }
};

export const ajax_getUserList = async (prefer) => {
  const result = await axios.get(`${url}/userlist?sex=${prefer}`);
  if (result.data) {
    return result.data;
  }
};

