export const ajax_updatePostComment = async (
  postId,
  commentUserId,
  head,
  time,
  username,
  comment,
  replyUserId = "empty",
  replyUsername = "empty",
  authorUnread = true,
  replyUnread = true
) => {
  const result = await axios.post(
    url + "/updatepostcomment",
    {
      postId,
      commentUserId,
      head,
      time,
      username,
      comment,
      replyUserId,
      replyUsername,
      authorUnread,
      replyUnread,
    },
    { withCredentials: true }
  );
  if (result.data) {
    return result.data;
  }
};

