import React, { useEffect, useState, useRef } from "react";
import io from "socket.io-client";
import { Switch, Route } from "react-router-dom";
import Chat from "../chat/chat";
import Message from "../message/message";
import HomePage from "../homepage/homePage";
import User from "../user/user";
import Header from "../../component/header/header";
import Navbar from "../../component/nav/nav";
import Post from "../post/post";
import NewComment from "../newComment/newComment";
import NewReply from "../newReply/newReply";
import postDetail from "../postDetail/postDetail";
import "./main.css";
import { connect } from "react-redux";
import {
  fetchingGetUser,
  fetchingGetUserList,
  fetchingReceiveChat,
  fetchingGetFriendList,
  fetchingHeaderName,
  fetchingUnread,
  fetchingGetPostList,
  fetchingGetCurrentUserFullPost,
  fetchingGetReplyPosts,
  fetchingGetViewWidth,
  fetchingGetInnerWidth,
  fetchingGetAuthorUnreadNumber,
  fetchingGetReplyUnreadNumber,
  fetchingUpdateNewCommentList,
  fetchingResetNewCommentReadSetting,
  fetchingUpdateNewReplyList,
  fetchingUpdateRedirect,
  fetchingGetChatList,
} from "../../redux/actions/actions";
import { getCookie } from "../../utils/checkCookie";
import Loading from "../../component/loading/loading";
import PersonalPage from "../personalPage/personalPage";
import isEqual from "lodash.isequal";
import PostDetail from "../postDetail/postDetail";

const ENDPOINT = "https://www.dreamgoalreality.top:4000";
window.socket = io(ENDPOINT);

const Main = (props) => {
  const [homeClicked, setHomepage] = useState(true);
  const [chatClicked, setChatpage] = useState(false);
  const [userClicked, setUserpage] = useState(false);
  const [chatUrl, setChatUrl] = useState("");
  const [friendlist, setfriendlist] = useState([]);
  const [loading, setLoading] = useState(false);
  const previousFullPost = usePrevious(props.currentUserFullPost);
  const previousRelyPosts = usePrevious(props.replyPosts);
  const mainRef = useRef();
  let totalUnread = 0;
  const userId = getCookie();

  function usePrevious(value) {
    const ref = useRef();
    useEffect(() => {
      ref.current = value;
    }, [value]);
    return ref.current;
  }

  useEffect(() => {
    if (props.user.username) {
      setLoading(true);
      props.fetchingGetPostList();
    } else {
      props.fetchingGetUser((result) => {
        if (!result.head) {
          props.fetchingUpdateRedirect("/info/complete");
          props.history.push("/info");
        }
      });
    }
  }, [props.user.username]);

  useEffect(() => {
    const element = mainRef.current;
    props.fetchingGetViewWidth(element);
  }, []);

  useEffect(() => {
    setLoading(false);
    if (props.postlist.length < 6) {
      window.scroll(0, 0);
    }
  }, [props.postlist.length]);

  useEffect(() => {
    props.fetchingGetFriendList(props.user._id);
    props.fetchingGetReplyPosts(props.user._id);
    props.fetchingGetCurrentUserFullPost(props.user._id);
  }, [props.user._id]);

  useEffect(() => {
    window.scroll(0, 0);
  }, [props.authorUnreadNumber, props.replyUnreadNumber]);

  useEffect(() => {
    if (
      !isEqual(previousRelyPosts, props.replyPosts) ||
      previousRelyPosts.length === 0
    ) {
      replyUnreadNumber();
      //window.scroll(0, 0);
    }
  }, [props.user._id, props.replyPosts]);

  useEffect(() => {
    if (
      !isEqual(previousFullPost, props.currentUserFullPost) ||
      previousFullPost.length === 0
    ) {
      authorUnreadNumber();
      props.fetchingResetNewCommentReadSetting();
      //window.scroll(0, 0);
    }
  }, [props.user._id, props.currentUserFullPost]);

  useEffect(() => {
    if (props.friendList.friendListInfo) {
      let { friendListInfo, finalRelatedChatlist } = props.friendList;
      let keyArray = Object.keys(finalRelatedChatlist);
      let relatedChatlistResult = keyArray.map((a) => {
        let unread = finalRelatedChatlist[a].filter(
          (a) => a.senderId !== props.user._id && a.read === false
        ).length;
        let latestInfo = finalRelatedChatlist[a].sort(
          (a, b) => b.time - a.time
        )[0];
        latestInfo.unread = unread;
        return {
          [a]: latestInfo,
        };
      });
      friendListInfo.forEach((a) => {
        for (const chat of relatedChatlistResult) {
          if (chat[a._id]) {
            a.latestMsg = chat[a._id].msg;
            a.time = chat[a._id].time;
            a.unread = chat[a._id].unread;
            totalUnread += chat[a._id].unread;
          }
        }
        props.fetchingUnread(totalUnread);
      });
      friendListInfo.sort((a, b) => b.time - a.time);
      setfriendlist(friendListInfo);
    }
  }, [JSON.stringify(props.friendList.finalRelatedChatlist)]);

  useEffect(() => {
    if (props.user._id) {
      window.socket.emit("join", { userid: props.user._id });

      window.socket.on("chatMsg", (data) => {
        props.fetchingGetChatList(data.chatId, "no", () => {});
        props.fetchingReceiveChat(data);
      });
    }

    return () => {
      window.socket.emit("disconnection");
      window.socket.off();
    };
  }, [ENDPOINT, props.user.username]);

  useEffect(() => {
    props.fetchingGetFriendList(props.user._id);
  }, [props.chat.time]);
  useEffect(() => {
    if (homeClicked) {
      props.history.replace("/homepage");
    } else if (chatClicked) {
      if (chatUrl) {
        props.history.push(chatUrl);
        setChatUrl("");
      } else {
        props.history.replace("/message");
      }
    } else {
      props.history.replace("/user");
    }
  }, [homeClicked, chatClicked, userClicked]);

  useEffect(() => {
    callback();
    window.addEventListener("resize", callback);
    return () => {
      window.removeEventListener("resize", callback);
    };
  }, []);

  useEffect(() => {
    if (!userId) {
      props.history.push("/login");
    }
  }, [userId]);

  const callback = () => {
    if (window.visualViewport.width < 800) {
      mainRef.current.style.width = `${window.visualViewport.width}px`;
    } else {
      mainRef.current.style.width = "800px";
    }
    props.fetchingGetViewWidth(mainRef.current);
    props.fetchingGetInnerWidth();
  };

  const authorUnreadNumber = () => {
    let unreadNumber = 0;
    let list = [];
    if (props.currentUserFullPost.length === 0) {
      return;
    }
    props.currentUserFullPost.forEach((v) => {
      if (v.comment.length !== 0) {
        v.comment.forEach((i) => {
          if (i.commentUserId !== props.user._id && i.authorUnread) {
            if (!list.find((l) => l._id === v._id)) {
              list.push(v);
            }
            unreadNumber++;
          }
        });
      }
    });
    props.fetchingGetAuthorUnreadNumber(unreadNumber);
    props.fetchingUpdateNewCommentList(list);
  };

  const replyUnreadNumber = () => {
    let unreadNumber = 0;
    let newReplyList = [];
    if (props.replyPosts.length === 0) {
      return 0;
    }
    props.replyPosts.forEach((v) => {
      if (
        v.replyUnread &&
        v.post.length !== 0 &&
        v.post[0].userId !== props.user._id
      ) {
        unreadNumber++;
        newReplyList.push(v);
      }
    });
    props.fetchingUpdateNewReplyList(newReplyList);
    props.fetchingGetReplyUnreadNumber(unreadNumber);
  };

  return (
    <div ref={mainRef} className="main background">
      <Header />
      <Switch>
        <Route
          exact
          path="/homepage"
          render={(props) => (
            <HomePage
              {...props}
              setHomepage={setHomepage}
              setChatpage={setChatpage}
              setUserpage={setUserpage}
              setChatUrl={setChatUrl}
            />
          )}
        />

        <Route
          path="/message"
          exact
          render={(props) => <Message {...props} friendlist={friendlist} />}
        />
        <Route
          path="/user"
          exact
          render={(props) => (
            <User
              {...props}
              setHomepage={setHomepage}
              setChatpage={setChatpage}
              setUserpage={setUserpage}
            />
          )}
        />
        <Route path="/chat/:userId" render={(props) => <Chat {...props} />} />
        <Route path="/post" component={Post} />
        <Route
          exact
          path="/personalpage/:userId"
          render={(props) => (
            <PersonalPage
              {...props}
              setHomepage={setHomepage}
              setChatpage={setChatpage}
              setUserpage={setUserpage}
              setChatUrl={setChatUrl}
            />
          )}
        />
        <Route path="/newcomment" component={NewComment} />
        <Route
          exact
          path="/newreply"
          render={(props) => (
            <NewReply
              {...props}
              setHomepage={setHomepage}
              setChatpage={setChatpage}
              setUserpage={setUserpage}
              setChatUrl={setChatUrl}
            />
          )}
        />
        <Route
          exact
          path="/postDetail/:postId"
          render={(props) => (
            <PostDetail
              {...props}
              setHomepage={setHomepage}
              setChatpage={setChatpage}
              setUserpage={setUserpage}
              setChatUrl={setChatUrl}
            />
          )}
        />
        <Route component={HomePage} />
      </Switch>
      {props.location.pathname.includes("/chat/") ? null : (
        <Navbar
          setHomepage={setHomepage}
          setChatpage={setChatpage}
          setUserpage={setUserpage}
          homeClicked={homeClicked}
          chatClicked={chatClicked}
          userClicked={userClicked}
        />
      )}
      {loading ? <Loading /> : null}
    </div>
  );
};

export default connect(
  (state) => ({
    user: state.user,
    headerName: state.headerName,
    friendList: state.friendList,
    chat: state.chat,
    chatlist: state.chatlist,
    trigger: state.render_trigger,
    postlist: state.postlist,
    currentUserFullPost: state.currentUserFullPost,
    replyPosts: state.replyPosts,
    authorUnreadNumber: state.authorUnreadNumber,
    replyUnreadNumber: state.replyUnreadNumber,
    newReplyList: state.newReplyList,
  }),
  {
    fetchingGetUser,
    fetchingGetUserList,
    fetchingReceiveChat,
    fetchingGetFriendList,
    fetchingHeaderName,
    fetchingUnread,
    fetchingGetPostList,
    fetchingGetCurrentUserFullPost,
    fetchingGetReplyPosts,
    fetchingGetViewWidth,
    fetchingGetInnerWidth,
    fetchingGetAuthorUnreadNumber,
    fetchingGetReplyUnreadNumber,
    fetchingUpdateNewCommentList,
    fetchingResetNewCommentReadSetting,
    fetchingUpdateNewReplyList,
    fetchingUpdateRedirect,
    fetchingGetChatList,
  }
)(Main);
