const postChange = (state = initPostChange, action) => {
  switch (action.type) {
    case UPDATE_POST_CHANGE:
      console.log(state);
      return { ...state, ...action.data };

    default:
      return state;
  }
};

export default combineReducers({
  user,
  userlist,
  chat,
  chatlist,
  receiver,
  headerName,
  friendList,
  unread,
  render_trigger,
  post,
  postlist,
  deletePost,
  updatePostLikeResult,
  postLikeList,
  updatePostCommentResult,
  postCommentList,
  currentUserFullPost,
  replyPosts,
  viewWidth,
  innerWidth,
  newCommentList,
  authorUnreadNumber,
  replyUnreadNumber,
  newCommentReadSettingResult,
  newReplyList,
  newReplyReadSettingResult,
  postChange,
});
