import React, { Component } from "react";
import "./header.css";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import {
  fetchingUpdateRedirect,
  fetchingHeaderName,
} from "../../redux/actions/actions";
import { removeCookie } from "../../utils/checkCookie";

class Header extends Component {
  constructor(props) {
    super(props);
  }
  handleClicked = (headerName) => {
    if (headerName.includes("💬")) {
      this.props.history.replace("/message");
    } else if (
      headerName === "post" ||
      headerName === "New Comment" ||
      headerName === "New Reply"
    ) {
      this.props.history.replace("/homepage");
    } else if (headerName.includes("page")) {
      this.props.history.goBack();
    } else if (headerName === "update") {
      this.props.history.push("/homepage");
      setTimeout(() => {
        window.location.reload();
      }, 0);
      // this.props.fetchingHeaderName("user");
      // this.props.history.goBack();
      this.props.fetchingUpdateRedirect("/homepage");
    } else if (headerName === "complete info") {
      this.props.fetchingUpdateRedirect("");
      removeCookie();
      this.props.history.push("/login");
    } else if (headerName.includes("detail")) {
      this.props.history.goBack();
    }
  };

  render() {
    return (
      <div
        className="header"
        style={{
          left: `${(this.props.innerWidth - this.props.viewWidth) / 2}px`,
          width: `${this.props.viewWidth}px`,
        }}
      >
        <h1>{this.props.headerName}</h1>
        {this.props.headerName.includes("💬") ||
        this.props.headerName === "post" ||
        this.props.headerName.includes("page") ||
        this.props.headerName === "New Comment" ||
        this.props.headerName === "New Reply" ||
        this.props.headerName === "update" ||
        this.props.headerName === "complete info" ||
        this.props.headerName === "detail" ? (
          <div
            className="goBack"
            onClick={() => this.handleClicked(this.props.headerName)}
          >
            <img
              src={require("../../assets/navImages/arrow.png")}
              alt="arrow"
            />
          </div>
        ) : null}
      </div>
    );
  }
}

export default connect(
  (state) => ({
    headerName: state.headerName,
    viewWidth: state.viewWidth,
    innerWidth: state.innerWidth,
  }),
  { fetchingUpdateRedirect, fetchingHeaderName }
)(withRouter(Header));
