import React, { Component, createRef } from "react";
import Header from "../../component/header/header";
import "./info.css";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import {
  fetchingUpdate,
  fetchingGetInnerWidth,
  fetchingGetViewWidth,
  fetchingGetUser,
  fetchingHeaderName,
} from "../../redux/actions/actions";
import { getCookie } from "../../utils/checkCookie";
import ImageUploader from "react-images-upload";
import { fetchingUploadProfile } from "../../utils/cloudinary";
import Loading from "../../component/loading/loading";
import { withRouter } from "react-router-dom";

class Info extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sex: "female",
      prefer: "female",
      detail: "",
      head: "",
      age: "",
      loading: false,
      buttonName: "save",
    };
    this.infoRef = createRef();
  }
  userId = getCookie();
  onDrop = async (picture) => {
    if (picture.length === 0) {
      return;
    } else {
      this.setState({ loading: true });
      const result = await fetchingUploadProfile(picture[0], "profile");
      this.setState({
        head: result.data.secure_url,
        loading: false,
      });
    }
  };

  handleChange = (name, value) => {
    if (name === "age") {
      if (value === "") {
        this.setState({
          [name]: value,
        });
      } else if (!isNaN(value)) {
        if (
          !value.includes(".") &&
          !value.includes(" ") &&
          parseInt(value) / 100 < 2
        ) {
          this.setState({
            [name]: value,
          });
        }
      }
    } else {
      this.setState({
        [name]: value,
      });
    }
  };
  handleClick = () => {
    this.props.fetchingUpdate(this.state);
  };

  callback = () => {
    if (this.infoRef.current) {
      if (window.visualViewport.width < 800) {
        this.infoRef.current.style.width = `${window.visualViewport.width}px`;
      }
      this.props.fetchingGetViewWidth(this.infoRef.current);
      this.props.fetchingGetInnerWidth();
    }
  };

  componentWillMount() {
    if (!this.userId) {
      this.props.history.replace("/login");
    } else if (this.props.user.redirect === "/info/complete") {
      this.props.fetchingHeaderName("complete info");
    } else if (this.props.user.redirect !== "/info") {
      this.props.fetchingGetUser(() => {
        if (!this.props.location.pathname.includes("update")) {
          if (this.props.user.head) {
            this.props.history.replace("/homepage");
            setTimeout(() => {
              window.location.reload();
            }, 0);
          }
          if (!this.userId) {
            this.props.history.replace("/login");
          }
        }
      });
    } else {
      this.props.fetchingHeaderName("update");
      this.setState({
        sex: this.props.user.sex,
        prefer: this.props.user.prefer,
        detail: this.props.user.detail,
        head: this.props.user.head,
        age: this.props.user.age,
        buttonName: "update",
      });
    }
  }

  componentDidMount() {
    this.callback();
    window.addEventListener("resize", this.callback);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.callback);
  }
  render() {
    if (
      this.props.user.redirect &&
      !this.props.user.redirect.includes("/info")
    ) {
      //return <Redirect to={this.props.user.redirect} />;
      this.props.history.push("/homepage");
      setTimeout(() => {
        window.location.reload();
      }, 0);
    }
    let profiles = [];
    for (let i = 1; i < 21; i++) {
      let object = {
        text: `profile${i}`,
        image: require(`../../assets/imges/profile/profile${i}.png`),
      };
      profiles.push(object);
    }
    let completed =
      this.state.sex &&
      this.state.head &&
      this.state.prefer &&
      this.state.detail &&
      this.state.age;
    return (
      <div
        ref={this.infoRef}
        className="info-wrap"
        style={{
          width: `${this.props.viewWidth}px`,
        }}
      >
        <Header header="complete details" />
        <div className="info-content">
          <div className="info-title">
            <span>Choose a profile image:</span>
            {this.state.head ? (
              this.state.head.includes("http") ? (
                <img
                  className="profilePhoto"
                  src={this.state.head}
                  alt={this.state.head}
                />
              ) : (
                <img
                  className="profilePhoto"
                  src={require(`../../assets/imges/profile/${this.state.head}.png`)}
                  alt={this.state.head}
                />
              )
            ) : null}
          </div>
          <div className="info-profiles">
            {profiles.map((v) => {
              return (
                <div className="info-image" key={v.text}>
                  <img
                    src={v.image}
                    alt={v.text}
                    onClick={(e) => this.handleChange("head", v.text)}
                  />
                  <p>{v.text}</p>
                </div>
              );
            })}
          </div>
          <div className="info-realProfile">
            <span>Or upload a real photo:</span>
            <ImageUploader
              className="info-upload"
              withIcon={true}
              onChange={this.onDrop}
              imgExtension={[".jpg", ".gif", ".png", ".gif", ".jpeg"]}
              maxFileSize={10000000}
              singleImage={true}
              buttonText="Choose a image"
            />
          </div>
          <div className="info">
            <div className="info-age">
              <span>age: </span>
              <input
                type="text"
                value={this.state.age}
                onChange={(e) => this.handleChange("age", e.target.value)}
              />
            </div>
            <div className="info-gender">
              <span>Gender: </span>
              <input
                type="radio"
                id="gender-female"
                value="female"
                onChange={(e) => this.handleChange("sex", e.target.value)}
                checked={this.state.sex === "female"}
              />
              <label htmlFor="gender-female">Female</label>
              <input
                type="radio"
                id="gender-male"
                value="male"
                onChange={(e) => this.handleChange("sex", e.target.value)}
                checked={this.state.sex === "male"}
              />
              <label htmlFor="gender-male">Male</label>
            </div>
            <div className="info-prefer">
              <span>Looking for: </span>
              <input
                type="radio"
                id="prefer-female"
                value="female"
                onChange={(e) => this.handleChange("prefer", e.target.value)}
                checked={this.state.prefer === "female"}
              />
              <label htmlFor="prefer-female">Female</label>
              <input
                type="radio"
                id="prefer-male"
                value="male"
                onChange={(e) => this.handleChange("prefer", e.target.value)}
                checked={this.state.prefer === "male"}
              />
              <label htmlFor="prefer-male">Male</label>
            </div>
            <div className="info-detail">
              <span className="lable-detail">Introduction:</span>
              <textarea
                id="detail"
                rows="4"
                cols="50"
                value={this.state.detail}
                onChange={(e) => this.handleChange("detail", e.target.value)}
              />
            </div>
          </div>
          <div className="info-save">
            <button
              className={completed ? "button" : "button disable"}
              onClick={this.handleClick}
              disabled={!completed}
            >
              {completed ? this.state.buttonName : "uncompleted"}
            </button>
          </div>
        </div>
        {this.state.loading ? <Loading /> : null}
      </div>
    );
  }
}

export default connect(
  (state) => ({
    user: state.user,
    viewWidth: state.viewWidth,
    innerWidth: state.innerWidth,
  }),
  {
    fetchingUpdate,
    fetchingGetInnerWidth,
    fetchingGetViewWidth,
    fetchingGetUser,
    fetchingHeaderName,
  }
)(withRouter(Info));
