import React, { Component, createRef } from "react";
import "./register.css";
import { connect } from "react-redux";
import { fetchingRegister } from "../../redux/actions/actions";
import { Redirect } from "react-router-dom";
import logo from "../../assets/imges/logo.jpeg";
import headerImage from "../../assets/imges/header.png";
import { removeCookie, getCookie } from "../../utils/checkCookie";
import Loading from "../../component/loading/loading";

class Register extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: "",
      passwordConfirm: "",
      logoWidth: "60%",
      loading: false,
      passwordChecking: true,
      error: "",
    };
    this.registerRef = createRef();
  }

  passwordCheck = (password) => {
    let regs = [/.{8,}/, /[A-Z]/, /[0-9]/];
    let result = regs.every((reg) => reg.test(password));
    if (result === false) {
      this.setState({
        error:
          "Password must be at least 8 characters and it must contain at least one number and one capital letter.",
        passwordChecking: false,
      });
    } else {
      this.setState({ error: "", passwordChecking: true });
    }
  };

  handleChange = (name, value) => {
    if (name === "password") {
      this.passwordCheck(value);
    }
    this.setState({ [name]: value });
  };
  clearState = () => {
    this.setState({
      username: "",
      password: "",
      passwordConfirm: "",
      innerWidth: 0,
      viewWidth: 0,
    });
  };
  handleClick = () => {
    if (!this.state.passwordChecking) {
      return;
    }
    this.setState({ loading: true });
    removeCookie();
    this.props.fetchingRegister({
      username: this.state.username,
      password: this.state.password,
      passwordConfirm: this.state.passwordConfirm,
    });
    const id = setInterval(() => {
      const userId = getCookie();
      if (this.props.user.msg) {
        this.setState({ loading: false });
        clearInterval(id);
      }
      if (userId) {
        this.setState({ loading: false });
        clearInterval(id);
        this.props.history.push("/info");
      }
    }, 1000);
  };

  handleGoBackClick = () => {
    this.props.history.replace("/login");
  };

  callback = () => {
    if (window.visualViewport.width < 600) {
      this.setState({
        logoWidth: "100%",
      });
    } else {
      this.setState({
        logoWidth: "60%",
      });
    }
    if (this.registerRef.current) {
      if (window.visualViewport.width < 800) {
        this.registerRef.current.style.width = `${window.visualViewport.width}px`;
      } else {
        this.registerRef.current.style.width = "800px";
      }
      this.setState({
        viewWidth: this.registerRef.current.getBoundingClientRect().width,
        innerWidth: window.visualViewport.width,
      });
    }
  };

  componentDidMount() {
    this.callback();
    window.addEventListener("resize", this.callback);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.callback);
  }

  render() {
    if (this.props.user.username) {
      //return <Redirect to={this.props.user.redirect} />;
      this.props.history.push("/homepage");
      setTimeout(() => {
        window.location.reload();
      }, 0);
    }
    return (
      <div ref={this.registerRef} className="register-wrap background">
        {this.state.loading ? <Loading /> : null}
        <div
          className="register-header"
          style={{
            left: `${(this.state.innerWidth - this.state.viewWidth) / 2}px`,
            width: `${this.state.viewWidth}px`,
          }}
        >
          <div className="goBack" onClick={this.handleGoBackClick}>
            <img
              src={require("../../assets/navImages/arrow.png")}
              alt="arrow"
            />
          </div>
          <img src={headerImage} alt="headerImage" />
        </div>
        <div className="logo">
          <img src={logo} alt="logo" style={{ width: this.state.logoWidth }} />
        </div>
        <div className="error">
          <span>{this.state.error || this.props.user.msg}</span>
        </div>
        <input
          className="input"
          type="text"
          placeholder="username"
          value={this.state.username}
          onChange={(e) => this.handleChange("username", e.target.value)}
        />
        <input
          className="input"
          type="password"
          placeholder="password"
          value={this.state.password}
          onChange={(e) => this.handleChange("password", e.target.value)}
        />
        <input
          className="input"
          type="password"
          placeholder="password again"
          value={this.state.passwordConfirm}
          onChange={(e) => this.handleChange("passwordConfirm", e.target.value)}
        />
        <button className="button" onClick={this.handleClick}>
          Register
        </button>
      </div>
    );
  }
}

export default connect(
  (state) => ({
    user: state.user,
  }),
  {
    fetchingRegister,
  }
)(Register);
