import React, { Component, createRef } from "react";
import "./homepage.css";
import Profile from "../../component/profile/profile";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import {
  fetchingGetPostList,
  fetchingUpdateNewCommentList,
  fetchingGetCurrentUserFullPost,
} from "../../redux/actions/actions";

class HomePage extends Component {
  constructor(props) {
    super(props);
    this.pagination = 0;
    this.loadMoreElement = createRef();
    this.observer = new IntersectionObserver(
      (entries) => {
        if (entries[0].boundingClientRect.y > 50) {
          if (entries[0].isIntersecting) {
            if (this.props.postlist[this.props.postlist.length - 1] !== "end") {
              props.fetchingGetPostList("", (this.pagination += 5));
            }
          }
        }
      },
      { threshold: 1 }
    );
  }
  handleClick = (indicator) => {
    if (indicator === "post") {
      this.props.history.replace("/post");
    } else {
      window.location.reload();
    }
  };

  handleNewCommentClick = () => {
    this.props.history.replace("/newcomment");
  };

  handleNewReplyClick = () => {
    this.props.history.replace("/newreply");
  };

  componentWillMount = () => {
    //this.props.fetchingGetCurrentUserFullPost(this.props.user._id);
  };

  componentDidMount() {
    this.observer.observe(this.loadMoreElement.current);
  }

  componentWillUnmount() {
    this.observer.unobserve(this.loadMoreElement.current);
  }

  render() {
    return (
      <div className="homepage-wrap">
        <div className="newPost-indicator">
          {this.props.authorUnreadNumber === 0 ? null : (
            <div
              className="indicator-body"
              onClick={this.handleNewCommentClick}
            >
              <span>{this.props.authorUnreadNumber}</span>
              <span> New Comment</span>
            </div>
          )}
          {this.props.replyUnreadNumber === 0 ? null : (
            <div className="indicator-body" onClick={this.handleNewReplyClick}>
              <span>{this.props.replyUnreadNumber}</span>
              <span> New Reply</span>
            </div>
          )}
        </div>
        <div
          className="post"
          onClick={() => this.handleClick("post")}
          style={{
            right: `${
              (this.props.innerWidth - this.props.viewWidth) / 2 + 15
            }px`,
          }}
        >
          <img src={require("../../assets/imges/camera.png")} alt="camera" />
        </div>
        <div
          className="refresh"
          onClick={() => this.handleClick("refresh")}
          style={{
            right: `${
              (this.props.innerWidth - this.props.viewWidth) / 2 + 55
            }px`,
          }}
        >
          <img src={require("../../assets/imges/refresh.png")} alt="refresh" />
        </div>
        {this.props.postlist.length !== 0 &&
        this.props.postlist[0] !== "nothing" &&
        this.props.headerName !== "user"
          ? this.props.postlist[this.props.postlist.length - 1] === "end"
            ? this.props.postlist
                .slice(0, this.props.postlist.length - 1)
                .map((value, index) => {
                  return (
                    <Profile
                      setHeadName={this.props.setHeadName}
                      setHomepage={this.props.setHomepage}
                      setChatpage={this.props.setChatpage}
                      setUserpage={this.props.setUserpage}
                      setChatUrl={this.props.setChatUrl}
                      key={index}
                      indicator={index}
                      userId={value.userId}
                      text={value.text}
                      time={value.time}
                      image={value.image}
                      username={value.username}
                      age={value.age}
                      head={value.head}
                      prefer={value.prefer}
                      sex={value.sex}
                      postId={value.postId}
                      value_postLikeList={value.postLikeList}
                      value_postCommentList={value.postCommentList}
                    />
                  );
                })
            : this.props.postlist.map((value, index) => {
                return (
                  <Profile
                    setHeadName={this.props.setHeadName}
                    setHomepage={this.props.setHomepage}
                    setChatpage={this.props.setChatpage}
                    setUserpage={this.props.setUserpage}
                    setChatUrl={this.props.setChatUrl}
                    key={index}
                    indicator={index}
                    userId={value.userId}
                    text={value.text}
                    time={value.time}
                    image={value.image}
                    username={value.username}
                    age={value.age}
                    head={value.head}
                    prefer={value.prefer}
                    sex={value.sex}
                    postId={value.postId}
                    value_postLikeList={value.postLikeList}
                    value_postCommentList={value.postCommentList}
                  />
                );
              })
          : null}
        <div ref={this.loadMoreElement} className="empty loadMore">
          {this.props.postlist[this.props.postlist.length - 1] === "end"
            ? "End"
            : "Loading"}
        </div>
        <div className="empty"></div>
      </div>
    );
  }
}

export default connect(
  (state) => ({
    headerName: state.headerName,
    userlist: state.userlist,
    user: state.user,
    postlist: state.postlist,
    currentUserFullPost: state.currentUserFullPost,
    replyPosts: state.replyPosts,
    viewWidth: state.viewWidth,
    innerWidth: state.innerWidth,
    authorUnreadNumber: state.authorUnreadNumber,
    replyUnreadNumber: state.replyUnreadNumber,
  }),
  {
    fetchingGetPostList,
    fetchingUpdateNewCommentList,
    fetchingGetCurrentUserFullPost,
  }
)(withRouter(HomePage));
