import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import {
  fetchingHeaderName,
  fetchingUpdateRedirect,
} from "../../redux/actions/actions";
import "./user.css";
import ProfileSex from "../../component/profileSex/profileSex";
import { removeCookie } from "../../utils/checkCookie";

const User = (props) => {
  const [sexualOrientation, setSexualOrientation] = useState("malefemale");

  const clearClickedState = (callback) => {
    props.setHomepage(false);
    props.setChatpage(false);
    props.setUserpage(false);
    callback();
  };

  const handleUpdateInfoClick = () => {
    props.fetchingUpdateRedirect("/info");
    props.history.push("/info/update");
  };

  const handleSignOutClick = () => {
    props.fetchingUpdateRedirect("");
    removeCookie();
    props.history.push("/login");
  };

  useEffect(() => {
    clearClickedState(() => {
      props.setUserpage(true);
    });
  }, []);

  useEffect(() => {
    if (props.sex === "female" && props.prefer === "female") {
      setSexualOrientation("les");
    } else if (props.sex === "male" && props.prefer === "male") {
      setSexualOrientation("gay");
    }
    props.fetchingHeaderName("user");
    window.scroll(0, 0);
  }, []);

  return (
    <div className="user-wrapper">
      <div className="user-content">
        <div className="user-profile">
          {props.user.head ? (
            props.user.head.includes("http") ? (
              <img src={props.user.head} alt={props.user.head} />
            ) : (
              <img
                src={require(`../../assets/imges/profile/${props.user.head}.png`)}
                alt={props.user.head}
              />
            )
          ) : null}
        </div>
        <div className="user-info">
          <ProfileSex
            username={props.user.username}
            sex={props.user.sex}
            age={props.user.age}
          />
          <span className="prefer-sex">Sexual Orientation</span>
          <img
            className="prefer-sex-img"
            src={require(`../../assets/imges/${sexualOrientation}.png`)}
          />
          <p className="detail">{props.user.detail}</p>
        </div>
      </div>
      <div className="buttons">
        <button className="button update-info" onClick={handleUpdateInfoClick}>
          update info
        </button>
        <button className="button sign-out" onClick={handleSignOutClick}>
          sign out
        </button>
      </div>
    </div>
  );
};

export default connect((state) => ({ user: state.user }), {
  fetchingHeaderName,
  fetchingUpdateRedirect,
})(User);
