import React, { Fragment, useState } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

import {
  fetchingUpdatePostComment,
  fetchingGetReceiver,
  fetchingUpdateReceiver,
  fetchngClearReceiver,
} from "../../redux/actions/actions";

const CommentContentReply = (props) => {
  const [replyInput, setReplyInput] = useState("");

  const keyUp = (e) => {
    if (e.keyCode === 13) {
      handleReplyInputCommentClick(props.v.commentUserId, props.v.username);
    }
  };

  const handleReplyInput = (e) => {
    setReplyInput(e.target.value);
  };

  const handleReplyInputCancelClick = () => {
    let elements = document.querySelectorAll(`[reply-identifier]`);
    elements.forEach((v) => {
      v.className = "replyInput-wrapper hide";
    });
  };

  const handleReplyClick = (_id, userId) => {
    if (!props.showMoreComments) {
      props.handleShowMoreCommentsClick();
    }
    handleReplyInputCancelClick();
    if (userId === props.user._id || !_id) {
      return;
    }
    let element = document.querySelector(`[reply-identifier="${_id}"]`);
    element.className = "replyInput-wrapper";
  };

  const handleReplyInputCommentClick = (replyUserId, replyUsername) => {
    if (!replyInput) {
      return;
    }
    handleReplyInputCancelClick();
    props.updatePostCommentList([
      ...props.postCommentList,
      {
        username: props.user.username,
        comment: replyInput,
        head: props.user.head,
        replyUserId,
        replyUsername,
      },
    ]);
    const time = new Date().getTime();
    props.fetchingUpdatePostComment(
      props.postId,
      props.user._id,
      props.user.head,
      time,
      props.user.username,
      replyInput,
      replyUserId,
      replyUsername
    );
  };

  const handleProfilePhotoClick = (e, id) => {
    e.stopPropagation();
    props.goToPersonalPage(id);
    // props.fetchngClearReceiver();
    // const receiverPersonalInfoList = props.postlist.filter(
    //   (v) => v.userId === id
    // );
    // props.fetchingUpdateReceiver(receiverPersonalInfoList);
    // props.history.push(`/personalpage/${id}`);
  };

  return (
    <Fragment>
      <div
        className="comment-content"
        onClick={() => handleReplyClick(props.v._id, props.v.commentUserId)}
      >
        {props.v.head ? (
          props.v.head.includes("http") ? (
            <img
              className="profilePhoto"
              src={props.v.head}
              alt={props.v.head}
              onClick={(e) => handleProfilePhotoClick(e, props.v.commentUserId)}
            />
          ) : (
            <img
              className="profilePhoto"
              src={require(`../../assets/imges/profile/${props.v.head}.png`)}
              alt={props.v.head}
              onClick={(e) => handleProfilePhotoClick(e, props.v.commentUserId)}
            />
          )
        ) : null}
        <div className="comment-content-left">
          <span className="username">
            <span
              onClick={(e) => handleProfilePhotoClick(e, props.v.commentUserId)}
            >
              {props.v.username}
            </span>
            <span className="reply"> replied </span>
            <span
              onClick={(e) => handleProfilePhotoClick(e, props.v.replyUserId)}
            >
              {props.v.replyUsername}:{" "}
            </span>
          </span>
          <p>{props.v.comment}</p>
        </div>
      </div>
      <div className="replyInput-wrapper hide" reply-identifier={props.v._id}>
        <textarea
          className="replyInput"
          placeholder={`${props.user.username} reply to ${props.v.username}: `}
          value={replyInput}
          onChange={handleReplyInput}
          onKeyUp={keyUp}
        />
        <div className="replyInput-buttons">
          <button
            className="replyInput-comment"
            onClick={() =>
              handleReplyInputCommentClick(
                props.v.commentUserId,
                props.v.username
              )
            }
          >
            Comment
          </button>
          <button
            className="replyInput-cancel"
            onClick={handleReplyInputCancelClick}
          >
            Cancel
          </button>
        </div>
      </div>
    </Fragment>
  );
};

export default connect(
  (state) => ({ user: state.user, postlist: state.postlist }),
  {
    fetchingUpdatePostComment,
    fetchingGetReceiver,
    fetchingUpdateReceiver,
    fetchngClearReceiver,
  }
)(withRouter(CommentContentReply));
