import React, { useEffect, Fragment } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { fetchingHeaderName } from "../../redux/actions/actions";
import Profile from "../../component/profile/profile";

const PostDetail = (props) => {
  useEffect(() => {
    window.scroll(0, 0);
    props.fetchingHeaderName("detail");
  }, [props.headerName]);

  if (!props.location.state) {
    return <Redirect to="/homepage" />;
  } else {
    return (
      <Fragment>
        <Profile
          key={props.location.state.v.postId}
          userId={props.location.state.v.userId}
          text={props.location.state.v.text}
          time={props.location.state.v.time}
          image={props.location.state.v.image}
          username={props.location.state.v.username}
          age={props.location.state.v.age}
          head={props.location.state.v.head}
          prefer={props.location.state.v.prefer}
          sex={props.location.state.v.sex}
          postId={props.location.state.v.postId}
          value_postLikeList={props.location.state.v.postLikeList}
          value_postCommentList={props.location.state.v.postCommentList}
          disable={true}
        />
        <div className="empty"></div>
      </Fragment>
    );
  }
};

export default connect(
  (state) => ({ headNname: state.headNname, user: state.user }),
  {
    fetchingHeaderName,
  }
)(PostDetail);
