import React, {
  useState,
  useEffect,
  useRef,
  useMemo,
  useLayoutEffect,
} from "react";
import { connect } from "react-redux";
import {
  fetchingStorChat,
  fetchingGetChatList,
  fetchingGetReceiver,
  fetchingHeaderName,
  fetchingDeleteChat,
  fetchngClearReceiver,
} from "../../redux/actions/actions";

import "./chat.css";

const Chat = (props) => {
  const ref = useRef();
  const emptyRef = useRef();
  const msgRef = useRef();
  const [input, setInput] = useState("");
  const [chatList, setChatList] = useState([{ chatId: "" }]);
  const [toggle, setToggle] = useState(false);
  const [scrollHeight, setScrollHeight] = useState("");
  const [goBottom, setGoBottom] = useState(false);
  const emojis = [
    "😀",
    "😁",
    "😂",
    "🤣",
    "😃",
    "😄",
    "😅",
    "😆",
    "😉",
    "😊",
    "😋",
    "😎",
    "😍",
    "😘",
    "🥰",
    "😗",
    "😙",
    "😚",
    "☺️",
    "🙂",
    "🤗",
    "🤩",
    "🤔",
    "🤨",
    "😐",
    "😑",
    "😶",
    "🙄",
    "😏",
    "😣",
    "😥",
    "😮",
    "🤐",
    "😯",
    "😪",
    "😫",
    "😴",
    "😌",
    "😛",
    "😜",
    "😝",
    "🤤",
    "😒",
    "😓",
    "😔",
    "😕",
    "🙃",
    "🤑",
    "😲",
    "☹️",
    "🙁",
    "😖",
    "😞",
    "😟",
    "😤",
    "😢",
    "😭",
    "😦",
    "😧",
    "😨",
    "😩",
    "🤯",
    "😬",
    "😰",
    "😱",
    "🥵",
    "🥶",
    "😳",
    "🤪",
    "😵",
    "😡",
    "😠",
    "🤬",
    "😷",
    "🤒",
    "🤕",
    "🤢",
    "🤮",
    "🤧",
    "😇",
    "🤠",
    "🤡",
    "🥳",
    "🥴",
    "🥺",
    "🤥",
    "🤫",
    "🤭",
    "🧐",
    "🤓",
    "😈",
    "👿",
    "👹",
    "👺",
    "💀",
    "👻",
    "👽",
    "🤖",
    "💩",
    "😺",
    "😸",
    "😹",
    "😻",
    "😼",
    "😽",
    "🙀",
    "😿",
    "😾",
  ];
  let senderId = props.user._id;
  let receiverId = props.match.params.userId;
  let Ids = [senderId, receiverId];
  let time = "";
  let msg = "";
  Ids.sort((a, b) => {
    if (a > b) {
      return -1;
    } else {
      return 1;
    }
  });
  let chatId = Ids.join("-");

  useEffect(() => {
    props.fetchingGetReceiver(receiverId);
    return () => {
      props.fetchingDeleteChat();
      props.fetchngClearReceiver();
      if (
        props.chatlist.length !== 0 &&
        props.chatlist[props.chatlist.length - 1].senderId !== props.user._id
      ) {
        props.fetchingGetChatList(chatId, "yes", () => {});
      }
    };
  }, []);

  useEffect(() => {
    props.fetchingHeaderName(`${props.receiver.username} 💬`);
  }, [props.receiver.username]);

  useEffect(() => {
    let mounted = true;
    if (mounted) {
      props.fetchingGetChatList(chatId, "not", (data) => {
        if (data.length !== 0) {
          setChatList([...data]);
        }
      });
    }
    return () => {
      mounted = false;
    };
  }, [chatId]);

  useEffect(() => {
    let mounted = true;
    if (mounted) {
      props.fetchingGetChatList(chatId, "not", () => {});
      setChatList((a) => {
        if (a[0].chatId) {
          if (
            a.filter((b) => b.time.toString() === props.chat.time.toString())
              .length !== 0
          ) {
            return [...a];
          }
          return [...a, props.chat];
        } else {
          return [props.chat];
        }
      });
      setGoBottom((a) => !a);
    }

    return () => {
      mounted = false;
    };
  }, [props.chatlist.length, props.chat.time]);

  useEffect(() => {
    if (
      props.chatlist.length !== 0 &&
      props.chatlist[props.chatlist.length - 1].senderId !== props.user._id
    ) {
      props.fetchingGetChatList(chatId, "yes", () => {});
    }
  }, [props.chatlist.length]);

  useLayoutEffect(() => {
    window.scrollTo(0, scrollHeight);
    setScrollHeight("");
  }, [scrollHeight, goBottom]);

  useEffect(() => {
    if (msgRef.current.scrollHeight > 600) {
      setScrollHeight(msgRef.current.scrollHeight);
    }
  });

  let Msg = useMemo(
    () => (
      <div ref={msgRef} className="chat-msgs" onClick={() => setToggle(false)}>
        {props.receiver
          ? chatList[0].chatId && props.receiver.head
            ? chatList.map((value, index) => {
                if (!value.senderId) {
                  return null;
                }
                if (value.senderId === senderId) {
                  return (
                    <div className="chat-send" key={index}>
                      <div className="msg">
                        <p>{value.msg}</p>
                      </div>
                      {props.user.head ? (
                        props.user.head.includes("http") ? (
                          <img
                            className="profilePhoto"
                            src={props.user.head}
                            alt={props.user.head}
                          />
                        ) : (
                          <img
                            className="profilePhoto"
                            src={require(`../../assets/imges/profile/${props.user.head}.png`)}
                            alt={props.user.head}
                          />
                        )
                      ) : null}
                    </div>
                  );
                } else {
                  return (
                    <div className="chat-receive" key={index}>
                      {props.receiver.head ? (
                        props.receiver.head.includes("http") ? (
                          <img
                            className="profilePhoto"
                            src={props.receiver.head}
                            alt={props.receiver.head}
                          />
                        ) : (
                          <img
                            className="profilePhoto"
                            src={require(`../../assets/imges/profile/${props.receiver.head}.png`)}
                            alt={props.receiver.head}
                          />
                        )
                      ) : null}
                      <div className="msg">
                        <p>{value.msg}</p>
                      </div>
                    </div>
                  );
                }
              })
            : null
          : null}
        <div ref={emptyRef} className="empty"></div>
      </div>
    ),
    [chatList, props.receiver.head]
  );

  const handleClicked = () => {
    setToggle(false);
    if (!input) {
      return;
    }
    time = new Date().getTime();
    msg = input;
    setInput("");
    props.fetchingStorChat({ chatId, senderId, time, msg }, () => {
      props.fetchingGetChatList(chatId, "not", () => {});
      sendSocketMsg();
    });
    ref.current.style.height = "35px";
    emptyRef.current.style.height = "52px";
  };

  const sendSocketMsg = () => {
    window.socket.emit("sendMsg", {
      chatId,
      receiverId,
      senderId,
      time,
      msg,
    });
  };

  const handleTextAreaChange = (e) => {
    if (e.target.value.includes("\n")) {
      return;
    }
    setInput(e.target.value);
    ref.current.style.height = "1px";
    ref.current.style.height = ref.current.scrollHeight + "px";
    if (ref.current.scrollHeight === 35) {
      emptyRef.current.style.height = "52px";
    } else {
      emptyRef.current.style.height = 15 + ref.current.scrollHeight + "px";
    }
  };

  const keyUp = (e) => {
    if (e.keyCode === 13) {
      handleClicked();
    }
  };

  return (
    <div className="chat-wrap">
      {Msg}
      <div
        className={toggle ? "chat-form-wrap toggleShow" : "chat-form-wrap"}
        style={{
          left: `${(props.innerWidth - props.viewWidth) / 2}px`,
          width: `${props.viewWidth}px`,
        }}
      >
        <div className="chat-form">
          <textarea
            ref={ref}
            type="text"
            value={input}
            onFocus={() => setToggle(false)}
            onChange={handleTextAreaChange}
            onKeyUp={(e) => keyUp(e)}
          />
          <span className="emoji" onClick={() => setToggle(!toggle)}>
            😊
          </span>
          <button onClick={handleClicked}>Send</button>
        </div>
        <div className="emoji-collection">
          {emojis.map((emoji, index) => {
            return (
              <span key={index} onClick={() => setInput((x) => x + emoji)}>
                {emoji}
              </span>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default connect(
  (state) => ({
    chat: state.chat,
    user: state.user,
    chatlist: state.chatlist,
    receiver: state.receiver,
    viewWidth: state.viewWidth,
    innerWidth: state.innerWidth,
  }),
  {
    fetchingStorChat,
    fetchingGetChatList,
    fetchingGetReceiver,
    fetchingHeaderName,
    fetchingDeleteChat,
    fetchngClearReceiver,
  }
)(Chat);
