import React, { useEffect } from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { fetchingDeleteChat } from "../../redux/actions/actions";
import { timeConverter } from "../../utils/timeConverter";

const Friend = (props) => {
  let time = timeConverter(props.time);

  useEffect(() => {
    props.fetchingDeleteChat();
  }, [props.unread]);

  const handleClicked = (receiverId) => {
    props.history.replace(`chat/${receiverId}`);
    window.socket.emit("joinReceiverRoom", { receiverId });
  };

  return (
    <div className="friend" onClick={() => handleClicked(props._id)}>
      <div className="friend-img">
        {props.unread === 0 ? null : (
          <div className="friend-indicator">
            <span>{props.unread}</span>
          </div>
        )}
        {props.head ? (
          props.head.includes("http") ? (
            <img className="profilePhoto" src={props.head} alt={props.head} />
          ) : (
            <img
              className="profilePhoto"
              src={require(`../../assets/imges/profile/${props.head}.png`)}
              alt={props.head}
            />
          )
        ) : null}
      </div>
      <div className="friend-info">
        <div className="friend-time">
          <span>{time}</span>
        </div>
        <div className="friend-info-top">
          <span>{props.username}</span>
        </div>
        <div className="friend-info-bottom">
          <span>{props.msg}</span>
        </div>
      </div>
    </div>
  );
};

export default connect(null, { fetchingDeleteChat })(withRouter(Friend));
