import React, { useState, useEffect, useRef } from "react";
import "./profile.css";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import {
  fetchingHeaderName,
  fetchingGetReceiver,
  fetchingUpdatePostLike,
  fetchingGetPostLike,
  fetchingUpdatePostComment,
  fetchingGetPostComment,
  fetchingUpdatePostChange,
  fetchingUpdateReceiver,
  fetchngClearReceiver,
  fetchingGetPostList,
} from "../../redux/actions/actions";
import { timeConverter } from "../../utils/timeConverter";
import Loading from "../loading/loading";
import CommentContentReply from "../commentContent/commentContentReply";
import CommentContent from "../../component/commentContent/commentContent";
import ProfileSex from "../profileSex/profileSex";

const Profile = (props) => {
  const [clicked, setClicked] = useState(false);
  const [postMoreMenueClassName, setPostMoreMenueClassName] = useState(
    "post-more-menue"
  );
  const [inputComment, setInputComment] = useState("");
  const [commentClicked, setCommentClicked] = useState(false);
  const [postLikeList, setPostLikeList] = useState([]);
  const [postCommentList, setPostCommentList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showMoreComments, setShowMoreComments] = useState(false);

  const time = timeConverter(props.time);
  const anchorRef = useRef();
  const postRef = useRef();

  useEffect(() => {
    props.fetchingHeaderName("Main");
  }, []);

  useEffect(() => {
    setPostLikeList(props.value_postLikeList);
    setPostCommentList(props.value_postCommentList);
  }, []);

  useEffect(() => {
    if (props.postChange[props.postId]) {
      setPostLikeList(props.postChange[props.postId].postLikeList);
      setPostCommentList(props.postChange[props.postId].postCommentList);
    }
  }, [JSON.stringify(props.postChange)]);

  const keyUp = (e) => {
    if (e.keyCode === 13) {
      handleCommentButtonClick();
    }
  };

  const handleShowMoreCommentsClick = () => {
    const element = document.querySelector(
      `.indicator_${props.indicator}.post-like-comment .comment`
    );
    if (showMoreComments) {
      setShowMoreComments(false);
      element.className = "comment comment-collapse";
      setTimeout(() => {
        postRef.current.scrollIntoView({
          block: "start",
          inline: "nearest",
        });
      }, 50);
    } else {
      setShowMoreComments(true);
      element.className = "comment comment-expand";
    }
  };

  const handleLikeClick = () => {
    const time = new Date().getTime();
    props.fetchingUpdatePostLike(
      props.postId,
      props.user._id,
      time,
      props.user.username
    );
    if (!postLikeList.find((v) => v.username === props.user.username)) {
      setPostLikeList([
        ...postLikeList,
        {
          username: props.user.username,
          likeUserId: props.user._id,
        },
      ]);
      props.fetchingUpdatePostChange({
        [props.postId]: {
          postLikeList: [
            ...postLikeList,
            {
              username: props.user.username,
              likeUserId: props.user._id,
            },
          ],
          postCommentList: [...postCommentList],
        },
      });
    }
  };

  const handleCommentClick = () => {
    setCommentClicked(true);
    setTimeout(() => {
      anchorRef.current.scrollIntoView({
        behavior: "smooth",
        block: "end",
        inline: "nearest",
      });
    }, 50);
  };

  const handleCommentButtonClick = () => {
    if (!inputComment) {
      return;
    }
    setCommentClicked(false);
    const time = new Date().getTime();
    setPostCommentList([
      ...postCommentList,
      {
        username: props.user.username,
        comment: inputComment,
        head: props.user.head,
        replyUserId: "empty",
        replyUsername: "empty",
      },
    ]);
    props.fetchingUpdatePostChange({
      [props.postId]: {
        postLikeList: [...postLikeList],
        postCommentList: [
          ...postCommentList,
          {
            username: props.user.username,
            comment: inputComment,
            head: props.user.head,
            replyUserId: "empty",
            replyUsername: "empty",
          },
        ],
      },
    });
    props.fetchingUpdatePostComment(
      props.postId,
      props.user._id,
      props.user.head,
      time,
      props.user.username,
      inputComment
    );

    setInputComment("");
  };

  const goToPersonalPage = (id) => {
    props.fetchngClearReceiver();
    // const receiverPersonalInfoList = props.postlist.filter(
    //   (v) => v.userId === id
    // );
    props.fetchingUpdateReceiver(id);
    props.history.push(`/personalpage/${id}`);
  };

  const handleCancelButtonClick = () => {
    setCommentClicked(false);
  };

  const handleInputCommentOnChange = (e) => {
    setInputComment(e.target.value);
  };

  const handleProfileContentClick = () => {
    if (postMoreMenueClassName === "post-more-menue expand-in") {
      setPostMoreMenueClassName("post-more-menue expand-out");
      setTimeout(() => {
        setPostMoreMenueClassName("post-more-menue");
      }, 500);
    }
  };

  const handleMoreClick = () => {
    if (postMoreMenueClassName === "post-more-menue") {
      setPostMoreMenueClassName("post-more-menue expand-in");
    } else {
      setPostMoreMenueClassName("post-more-menue expand-out");
      setTimeout(() => {
        setPostMoreMenueClassName("post-more-menue");
      }, 500);
    }
  };

  const handleClicked = (receiverId) => {
    clearClickedState(() => {
      props.setChatUrl(`/chat/${receiverId}`);
      props.setChatpage(true);
      window.socket.emit("joinReceiverRoom", { receiverId });
    });
  };

  const handlePostCommentListUpdate = (input) => {
    setPostCommentList(input);
    props.fetchingUpdatePostChange({
      [props.postId]: {
        postLikeList: [...postLikeList],
        postCommentList: [...input],
      },
    });
  };

  const clearClickedState = (callback) => {
    props.setHomepage(false);
    props.setChatpage(false);
    props.setUserpage(false);
    callback();
  };

  const renderComponent = () => {
    const result = postCommentList.map((v, i) => {
      if (v.replyUserId !== "empty") {
        return (
          <CommentContentReply
            v={v}
            key={i}
            postId={props.postId}
            postCommentList={postCommentList}
            updatePostCommentList={handlePostCommentListUpdate}
            handleShowMoreCommentsClick={handleShowMoreCommentsClick}
            showMoreComments={showMoreComments}
            goToPersonalPage={goToPersonalPage}
          />
        );
      } else {
        return (
          <CommentContent
            v={v}
            key={i}
            postId={props.postId}
            postCommentList={postCommentList}
            updatePostCommentList={handlePostCommentListUpdate}
            handleShowMoreCommentsClick={handleShowMoreCommentsClick}
            showMoreComments={showMoreComments}
            goToPersonalPage={goToPersonalPage}
          />
        );
      }
    });
    return result;
  };

  if (loading) {
    return <Loading />;
  } else {
    return (
      <div
        ref={postRef}
        className="profile-content"
        onClick={handleProfileContentClick}
      >
        {props.userId === props.user._id || props.disable ? null : (
          <div
            className="profile-talk"
            onClick={() => handleClicked(props.userId)}
          >
            <img
              className="profile-chat"
              src={require("../../assets/navImages/chat.png")}
              alt="chat"
            />
          </div>
        )}
        <div
          className="profile-top"
          onClick={() => {
            goToPersonalPage(props.userId);
          }}
        >
          <div className="profile-left">
            {props.head ? (
              props.head.includes("http") ? (
                <img
                  className="profilePhoto"
                  src={props.head}
                  alt={props.head}
                />
              ) : (
                <img
                  className="profilePhoto"
                  src={require(`../../assets/imges/profile/${props.head}.png`)}
                  alt={props.head}
                />
              )
            ) : null}
          </div>
          <div className="profile-right">
            <ProfileSex
              username={props.username}
              sex={props.sex}
              age={props.age}
            />
          </div>
        </div>
        <div className="profile-post">
          <p>{props.text}</p>
          <div className="post-image">
            <div
              className={clicked ? "zoom-click" : "zoom-pic"}
              style={{
                left: `${(props.innerWidth - props.viewWidth) / 2}px`,
                width: `${props.viewWidth}px`,
              }}
              onClick={() => setClicked(false)}
            >
              <img
                className="zoom"
                src={props.image}
                alt="zoom"
                style={{
                  left: `${(props.innerWidth - props.viewWidth) / 2}px`,
                  width: `${props.viewWidth}px`,
                }}
              />
            </div>
            <img
              className="post-origin"
              src={props.image}
              alt="post"
              onClick={() => setClicked(true)}
            />
            <div className="post-info">
              <span>{time}</span>
              <div className="post-more">
                <div className={postMoreMenueClassName}>
                  <div className="post-more-like" onClick={handleLikeClick}>
                    <img
                      src={require("../../assets/imges/heart.png")}
                      alt="heart"
                    />
                    <span>Like</span>
                  </div>
                  <div
                    className="post-more-comment"
                    onClick={handleCommentClick}
                  >
                    <img
                      src={require("../../assets/imges/comment.png")}
                      alt="comment"
                    />
                    <span>Comment</span>
                  </div>
                </div>
                <img
                  src={require("../../assets/imges/more.png")}
                  alt="more"
                  onClick={handleMoreClick}
                />
              </div>
            </div>
            <div className={`post-like-comment indicator_${props.indicator}`}>
              {postLikeList && postLikeList.length !== 0 ? (
                <div className="like">
                  <img
                    src={require("../../assets/imges/heart.png")}
                    alt="heart"
                  />
                  {postLikeList.map((v, i) => (
                    <span
                      className="username"
                      key={i}
                      onClick={() => {
                        goToPersonalPage(v.likeUserId);
                      }}
                    >
                      {v.username},
                    </span>
                  ))}
                </div>
              ) : null}
              {postCommentList && postCommentList.length !== 0 ? (
                <div className="comment comment-collapse">
                  <div className="comment-list">{renderComponent()}</div>
                </div>
              ) : null}
              {postCommentList && postCommentList.length > 2 ? (
                showMoreComments ? (
                  <span onClick={handleShowMoreCommentsClick}>
                    collapse comments
                  </span>
                ) : (
                  <span onClick={handleShowMoreCommentsClick}>
                    show more comments
                  </span>
                )
              ) : null}
              {commentClicked ? (
                <div className="input-wrapper" id="input-wrapper">
                  <textarea
                    className="inputComment"
                    value={inputComment}
                    onChange={handleInputCommentOnChange}
                    onKeyUp={keyUp}
                  />
                  <div className="comment-button-wrapper">
                    <button
                      className="button comment-button"
                      onClick={handleCommentButtonClick}
                    >
                      Comment
                    </button>
                    <button
                      className="button comment-button"
                      onClick={handleCancelButtonClick}
                    >
                      Cancel
                    </button>
                  </div>
                  <div id="title-element" style={{ position: "relative" }}>
                    <div
                      ref={anchorRef}
                      id="anchor-name"
                      style={{ position: "absolute", top: "400px", left: 0 }}
                    ></div>
                  </div>
                </div>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    );
  }
};

export default connect(
  (state) => ({
    user: state.user,
    postLikeList: state.postLikeList,
    currentUserFullPost: state.currentUserFullPost,
    viewWidth: state.viewWidth,
    innerWidth: state.innerWidth,
    postlist: state.postlist,
    postChange: state.postChange,
  }),
  {
    fetchingHeaderName,
    fetchingGetReceiver,
    fetchingUpdatePostLike,
    fetchingGetPostLike,
    fetchingUpdatePostComment,
    fetchingGetPostComment,
    fetchingUpdatePostChange,
    fetchingUpdateReceiver,
    fetchngClearReceiver,
    fetchingGetPostList,
  }
)(withRouter(Profile));
