import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  fetchingHeaderName,
  fetchingGetReplyPosts,
  fetchingUpdateNewReplyReadSetting,
  fetchingClearUnreadNum,
} from "../../redux/actions/actions";
import Profile from "../../component/profile/profile";
import "./newReply.css";

const NewReply = (props) => {
  const [replyPostList, setReplyPostList] = useState([]);
  useEffect(() => {
    props.fetchingHeaderName("New Reply");
  }, [props.headerName]);

  useEffect(() => {
    let _ids = [];
    props.newReplyList.forEach((v) => {
      _ids.push(v._id);
    });
    props.fetchingUpdateNewReplyReadSetting(_ids);
    return () => {
      props.fetchingClearUnreadNum("reply");
    };
  }, []);

  useEffect(() => {
    let array = [];
    props.newReplyList.forEach((v) => {
      if (!array.some((j) => j.postId === v.postId)) {
        array.push(v);
      }
    });
    setReplyPostList(array);
  }, [props.newReplyList.length]);

  //   useEffect(() => {
  //     if (props.newReplyList.length !== 0) {
  //       props.newReplyList.foreach((v) => {
  //         props.fetchingUpdateNewReplyReadSetting(v._id, props.user._id);
  //       });
  //     }
  //   }, [props.newReplyList.length]);
  return (
    <div className="newReply-wrapper">
      {replyPostList.map((v, index) => (
        <Profile
          setHeadName={props.setHeadName}
          setHomepage={props.setHomepage}
          setChatpage={props.setChatpage}
          setUserpage={props.setUserpage}
          setChatUrl={props.setChatUrl}
          key={index}
          userId={v.userInfo[0]._id}
          text={v.post[0].text}
          time={v.post[0].time}
          image={v.post[0].image}
          username={v.userInfo[0].username}
          age={v.userInfo[0].age}
          head={v.userInfo[0].head}
          prefer={v.userInfo[0].prefer}
          sex={v.userInfo[0].sex}
          postId={v.post[0]._id}
          value_postLikeList={v.postLikeList}
          value_postCommentList={v.postCommentList}
          disable={true}
        />
      ))}
    </div>
  );
};

export default connect(
  (state) => ({
    user: state.user,
    newReplyList: state.newReplyList,
    headerName: state.headerName,
  }),
  {
    fetchingHeaderName,
    fetchingGetReplyPosts,
    fetchingUpdateNewReplyReadSetting,
    fetchingClearUnreadNum,
  }
)(NewReply);
