import React, { useEffect } from "react";
import { connect } from "react-redux";
import {
  fetchingHeaderName,
  fetchingGetFriendList,
  fetchingUnread,
} from "../../redux/actions/actions";
import Friend from "../../component/friend/friend";
import "./message.css";

const Message = (props) => {
  useEffect(() => {
    props.fetchingGetFriendList(props.user._id);
    props.fetchingHeaderName("message");
    window.scrollTo(0, 0);
  }, [props.chat.time, props.chatlist.length]);

  useEffect(() => {
    window.scrollTo(0, 0);
  });
  return (
    <div className="message-wrap">
      <div className="message-collection">
        {props.friendlist !== []
          ? props.friendlist.map((a, index) => (
              <Friend
                head={a.head}
                username={a.username}
                msg={a.latestMsg}
                time={a.time}
                key={index}
                _id={a._id}
                unread={a.unread}
              />
            ))
          : null}
      </div>
    </div>
  );
};

export default connect(
  (state) => ({
    friendList: state.friendList,
    user: state.user,
    chat: state.chat,
    chatlist: state.chatlist,
  }),
  { fetchingHeaderName, fetchingGetFriendList, fetchingUnread }
)(Message);
