import moment from "moment";

export const timeConverter = (inputTime) => {
  let time = "";

  const date = moment().day("Monday").format("DD/M/YYYY");
  const year = date.split("/")[2];
  const month = date.split("/")[1];
  const day = date.split("/")[0];
  const sunday = new Date(year, month - 1, day);
  const sunday_milliseconds = moment(sunday).unix() * 1000;

  const today_date = moment().format("MM/DD/YYYY");
  const today_start = new Date(`${today_date} 00:00:00`);
  const today_milliseconds = moment(today_start).unix() * 1000;

  if (parseInt(inputTime) > today_milliseconds) {
    time = moment(parseInt(inputTime)).format("LT");
  } else if (
    parseInt(inputTime) > sunday_milliseconds &&
    parseInt(inputTime) < today_milliseconds
  ) {
    const result = moment(parseInt(inputTime)).format("dddd");
    switch (result) {
      case "Monday":
        time = "Mon";
        break;
      case "Tuesday":
        time = "Tue";
        break;
      case "Wednesday":
        time = "Wed";
        break;
      case "Thursday":
        time = "Thu";
        break;
      case "Friday":
        time = "Fri";
        break;
      case "Saturday":
        time = "Sat";
        break;
      case "Sunday":
        time = "Sun";
        break;
      default:
        return;
    }
  } else {
    time = moment(parseInt(inputTime)).format("DD/MM");
  }

  return time;
};
