import React, { useEffect, useState, Fragment, useRef } from "react";
import "./personalPage.css";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import {
  fetchingHeaderName,
  fetchingUpdate,
  fetchingDeletePost,
  fetchingGetPostList,
  fetchngClearReceiver,
} from "../../redux/actions/actions";
import { timeConverter } from "../../utils/timeConverter";
import ImageUploader from "react-images-upload";
import { fetchingUploadProfile } from "../../utils/cloudinary";
import Loading from "../../component/loading/loading";

const PersonalPage = (props) => {
  const [personalPostlist, setPersonalPostlist] = useState([]);
  const [click, setClick] = useState(false);
  const [selectedPic, setSelectedPic] = useState("");
  const [errorMsg, setErrorMsg] = useState("");
  const [backgroundPic, setBackgroundPic] = useState("");
  const [loading, setLoading] = useState(false);
  const [text, setText] = useState("");
  const [likeListNum, setLikeListNum] = useState(0);
  const [commentListNum, setCommentListNum] = useState(0);
  const [menueClassName, setMenueClassName] = useState("more-menue");
  const [postId, setPostId] = useState("");
  const [index, setIndex] = useState(0);
  const fulltext = useRef();
  const userId = props.match.params.userId;

  const onDrop = async (picture) => {
    if (picture.length === 0) {
      setErrorMsg(document.querySelector(".errorClass").innerHTML);
      return;
    } else {
      setLoading(true);
      const result = await fetchingUploadProfile(
        picture[0],
        "personalPage_background"
      );
      const url = result.data.secure_url;
      setBackgroundPic(url);
      setLoading(false);
      props.fetchingUpdate({ background: url });
    }
  };

  const handleHeartCommentClick = (e) => {
    e.stopPropagation();
    props.history.push({
      pathname: `/postDetail/${postId}`,
      state: { v: personalPostlist[index] },
    });
  };

  const handleBackgroundClick = () => {
    const button = document.querySelector(".chooseFileButton");
    if (button) {
      button.click();
    }
  };

  const handleCloseClick = () => {
    const errorBox = document.querySelector(".errorBox");
    errorBox.style.animation = "error-out 1s ease forwards";
    setTimeout(() => {
      setErrorMsg("");
    }, 1000);
  };

  const handleMoreOnClick = (e) => {
    e.stopPropagation();
    setMenueClassName("more-menue slide-up");
  };

  const handleCancleClick = (e) => {
    e.stopPropagation();
    setMenueClassName("more-menue slide-down");
    setTimeout(() => {
      setMenueClassName("more-menue");
    }, 500);
  };

  const handleDeleteClick = (e) => {
    e.stopPropagation();
    setLoading(true);
    props.fetchingDeletePost(postId, () => {
      setMenueClassName("more-menue");
      setLoading(false);
      props.fetchingGetPostList();
      handleZoomClick();
    });
  };

  const handleZoomClick = () => {
    setClick(false);
    setSelectedPic("");
    setText("");
  };

  const handleClicked = (receiverId) => {
    clearClickedState(() => {
      props.setChatUrl(`/chat/${receiverId}`);
      props.setChatpage(true);
      window.socket.emit("joinReceiverRoom", { receiverId });
    });
  };

  const clearClickedState = (callback) => {
    props.setHomepage(false);
    props.setChatpage(false);
    props.setUserpage(false);
    callback();
  };

  useEffect(() => {
    if (!props.receiver.length) {
      setLoading(true);
    } else {
      setLoading(false);
      setPersonalPostlist(props.receiver);
      setBackgroundPic(props.receiver[0].background);
      props.fetchingHeaderName(`${props.receiver[0].username}'s page`);
    }
  }, [JSON.stringify(props.postlist.length), JSON.stringify(props.receiver)]);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  if (!props.user.username) {
    props.history.push("/homepage");
    return null;
  } else if (!props.receiver[0]) {
    return loading ? <Loading /> : null;
  } else {
    return (
      <div className="page-wrapper">
        {props.receiver[0].userId === props.user._id || props.disable ? null : (
          <div
            className="personalPage-talk"
            onClick={() => handleClicked(props.receiver[0].userId)}
            style={{
              right: `${(props.innerWidth - props.viewWidth) / 2 + 15}px`,
            }}
          >
            <img
              className="personalPage-chat"
              src={require("../../assets/navImages/chat.png")}
              alt="chat"
            />
          </div>
        )}

        {loading ? <Loading /> : null}
        {errorMsg ? (
          <div className="errorBox">
            <span className="error-close" onClick={handleCloseClick}>
              X
            </span>
            <span className="error-msg">{errorMsg}</span>
          </div>
        ) : null}
        <div className="page-top">
          <div className="page-background" onClick={handleBackgroundClick}>
            {props.match.params.userId === props.user._id ? (
              <Fragment>
                <ImageUploader
                  className="page-background-uploader"
                  withIcon={true}
                  onChange={onDrop}
                  imgExtension={[".jpg", ".gif", ".png", ".gif", ".jpeg"]}
                  maxFileSize={5242880}
                  singleImage={true}
                  withPreview={true}
                  buttonText="Choose a image"
                  errorClass="errorClass"
                />
                {backgroundPic ? null : <span>Click to upload a cover</span>}
              </Fragment>
            ) : null}

            {backgroundPic ? (
              <img src={backgroundPic} alt="cover" />
            ) : (
              <img
                src={require("../../assets/imges/page-background.jpg")}
                alt="cover"
              />
            )}
          </div>
        </div>
        <div className="page-middle">
          <div className="page-middle-content">
            <div className="page-middle-top">
              <span>{props.receiver[0].username}</span>
              {props.receiver[0].head ? (
                props.receiver[0].head.includes("http") ? (
                  <img
                    src={props.receiver[0].head}
                    alt={props.receiver[0].head}
                  />
                ) : (
                  <img
                    src={require(`../../assets/imges/profile/${props.receiver[0].head}.png`)}
                    alt={props.receiver[0].head}
                  />
                )
              ) : null}
            </div>
            <div className="page-middle-bottom">
              <p>{props.receiver[0].detail}</p>
            </div>
          </div>
        </div>
        <div className="page-content-empty"></div>
        {personalPostlist.map((v, i) => {
          return (
            <Fragment key={i}>
              <div className="page-content">
                <div className="page-content-body">
                  <span className="time">{timeConverter(v.time)}</span>
                  <img
                    onClick={(e) => {
                      setClick(true);
                      setSelectedPic(e.target.src);
                      setText(e.target.getAttribute("text"));
                      setPostId(e.target.getAttribute("postId"));
                      setLikeListNum(e.target.getAttribute("likenum"));
                      setCommentListNum(e.target.getAttribute("commentnum"));
                      setIndex(e.target.getAttribute("index"));
                    }}
                    index={i}
                    src={v.image}
                    alt="post"
                    text={v.text}
                    postid={v.postId}
                    likenum={v.postLikeList.length}
                    commentnum={v.postCommentList.length}
                  />
                  {click ? (
                    <div
                      className="page-content-body-zoom"
                      onClick={handleZoomClick}
                      style={{
                        right: `${(props.innerWidth - props.viewWidth) / 2}px`,
                        width: `${props.viewWidth}px`,
                      }}
                    >
                      <img
                        className="zoomPic"
                        src={selectedPic}
                        alt="zoom"
                        style={{
                          width: `${props.viewWidth}px`,
                        }}
                      />
                      {props.receiver[0].userId === props.user._id ? (
                        <img
                          onClick={handleMoreOnClick}
                          className="more"
                          src={require("../../assets/imges/more.png")}
                          alt="more"
                        />
                      ) : null}
                      <div
                        ref={fulltext}
                        className="fullText"
                        style={{
                          width: `${props.viewWidth}px`,
                        }}
                        onClick={(e) => {
                          e.stopPropagation();
                          if (
                            fulltext.current.style.animation.includes(
                              "error-out"
                            )
                          ) {
                            fulltext.current.style.animation =
                              "error-in 0.2s forwards";
                          } else {
                            fulltext.current.style.animation =
                              "error-out 0.2s forwards";
                          }
                        }}
                      >
                        <p>{text}</p>
                        <div className="heart-comment" index={i}>
                          <img
                            src={require("../../assets/imges/heart.png")}
                            alt="heart"
                            onClick={(e) => handleHeartCommentClick(e)}
                          />

                          <span onClick={(e) => handleHeartCommentClick(e)}>
                            {likeListNum}
                          </span>
                          <img
                            src={require("../../assets/imges/comment.png")}
                            alt="comment"
                            onClick={(e) => handleHeartCommentClick(e)}
                          />
                          <span onClick={(e) => handleHeartCommentClick(e)}>
                            {commentListNum}
                          </span>
                        </div>
                      </div>
                      <div className={menueClassName}>
                        <div
                          className="delete-wrapper"
                          onClick={handleDeleteClick}
                        >
                          <span>Delete</span>
                        </div>
                        <div
                          className="cancel-wrapper"
                          onClick={handleCancleClick}
                        >
                          <span>Cancel</span>
                        </div>
                      </div>
                    </div>
                  ) : null}

                  <p className="ellipsisText">{v.text}</p>
                </div>
              </div>
            </Fragment>
          );
        })}
      </div>
    );
  }
};

export default connect(
  (state) => ({
    postlist: state.postlist,
    user: state.user,
    deletePost: state.deletePost,
    receiver: state.receiver,
    viewWidth: state.viewWidth,
    innerWidth: state.innerWidth,
  }),
  {
    fetchingHeaderName,
    fetchingUpdate,
    fetchingDeletePost,
    fetchingGetPostList,
    fetchngClearReceiver,
  }
)(withRouter(PersonalPage));
