const register_success = (user) => ({ type: REGISTER, data: user });
const error = (errorMsg) => ({ type: ERROR_MSG, msg: errorMsg });
const update_success = (user) => ({ type: UPDATE, data: user });
const get_userlist = (userlist) => ({ type: USERLIST, data: userlist });
const get_user = (user) => ({ type: USER, data: user });
const login_success = (user) => ({ type: LOGIN, data: user });
const store_chat = (chat) => ({
  type: CHAT,
  data: chat,
});
const receive_chat = (chat) => ({ type: RECEIVE_CHAT, data: chat });
const delete_chat = () => ({ type: DELETE_CHAT, data: null });
const get_chatlist = (chatlist) => ({ type: CHATLIST, data: chatlist });
const get_receiver = (receiver) => ({ type: RECEIVER, data: receiver });
const update_headerName = (headerName) => ({
  type: HEADERNAME,
  data: headerName,
});

const get_friendlist = (result) => ({ type: FRIEDNLIST, data: result });
const get_unread = (result) => ({ type: UNREAD_NUM, data: result });
const render_trigger = () => ({ type: RENDER_TRIGGER });
const post = (result) => ({ type: POST, data: result });
const clear_post = () => ({ type: CLEAR_POST });
const get_postlist = (postlist) => ({ type: GET_POSTLIST, data: postlist });
const clear_receiver = () => ({ type: CLEAR_RECEIVER });
const delete_post = (result) => ({ type: DELETE_POST, data: result });
const update_post_like = (result) => ({ type: UPDATE_POST_LIKE, data: result });
const get_post_like = (result) => ({ type: GET_POST_LIKE, data: result });
const update_post_comment = (result) => ({
  type: UPDATE_POST_COMMENT,
  data: result,
});
const get_post_comment = (result) => ({ type: GET_POST_COMMENT, data: result });
const get_current_user_full_post = (result) => ({
  type: GET_CURRENTUSER_FULL_POST,
  data: result,
});

const get_reply_post = (result) => ({ type: GET_REPLY_POSTS, data: result });
const get_view_width = (result) => ({ type: GET_VIEW_WIDTH, data: result });
const get_inner_width = (result) => ({ type: GET_INNER_WIDTH, data: result });
const update_newComment_list = (result) => ({
  type: UPDATE_NEWCOOMENT_LIST,
  data: result,
});
const get_authorUnreadNumber = (result) => ({
  type: GET_AUTHORUNREADNUMBER,
  data: result,
});
const get_replyUnreadNumber = (result) => ({
  type: GET_REPLYUNREADNUMBER,
  data: result,
});
const update_newComment_readSetting = (result) => ({
  type: UPDATE_NEWCOMMENT_READSETTING,
  data: result,
});

const reset_newComment_readSetting = (result) => ({
  type: RESET_NEWCOMMENT_READSETTING,
  data: result,
});
const update_newReply_list = (result) => ({
  type: UPDATE_NEWREPLY_LIST,
  data: result,
});
const update_newReply_readSetting = (result) => ({
  type: UPDATE_NEWREPLY_READSETTING,
  data: result,
});
const update_redirect = (result) => ({
  type: UPDATE_REDIRECT,
  data: result,
});
const update_post_change = (result) => ({
  type: UPDATE_POST_CHANGE,
  data: result,
});
const clear_unread_num = (name) => {
  if (name === "reply") {
    return {
      type: CLEAR_REPLY_UNREAD_NUM,
      data: 0,
    };
  } else {
    return {
      type: CLEAR_COMMENT_UNREAD_NUM,
      data: 0,
    };
  }
};

const update_receiver = (result) => ({
  type: UPDATE_RECEIVER,
  data: result,
});

export const fetchingRegister = (user) => {
  if (!user.username || !user.password) {
    return error("username or password is empty");
  }
  if (user.password !== user.passwordConfirm) {
    return error("password does not match");
  }
  return async (dispacth) => {
    let result = await ajax_register(user.username, user.password);
    if (result.code === 1) {
      dispacth(error(result.msg));
    } else {
      dispacth(register_success(result.data));
    }
  };
};

export const fetchingUpdate = (info) => {
  return async (dispacth) => {
    let result = await ajax_update(info);
    if (result.code === 1) {
      dispacth(error(result.msg));
    } else {
      dispacth(update_success(result.data));
    }
  };
};

export const fetchingGetUserList = (prefer) => {
  return async (dispacth) => {
    let result = await ajax_getUserList(prefer);
    if (result.code === 0) {
      dispacth(get_userlist(result.data));
    }
  };
};

export const fetchingGetUser = (callback) => {
  return async (dispacth) => {
    let result = await ajax_getUser();
    if (result.code === 0) {
      dispacth(get_user(result.data));
      callback(result.data);
    }
  };
};

