export const fetchingStorChat = (chat, callback) => {
  return async (dispacth) => {
    let result = await ajax_updateChat(chat);
    if (result.code === 0) {
      dispacth(store_chat(result.data));
    }
    callback();
  };
};

export const fetchingGetChatList = (chatId, resetRead, callback) => {
  return async (dispacth) => {
    let result = await ajax_getChatList(chatId, resetRead);
    if (result.code === 0) {
      callback(result.data);
      dispacth(get_chatlist(result.data));
    }
  };
};

