import React, { useState, useEffect } from "react";
import "./post.css";
import ImageUploader from "react-images-upload";
import { connect } from "react-redux";
import {
  fetchingHeaderName,
  fetchingPost,
  fetchingClearPost,
  fetchingGetPostList,
} from "../../redux/actions/actions";
import { fetchingUploadProfile } from "../../utils/cloudinary";
import Loading from "../../component/loading/loading";
import { withRouter } from "react-router-dom";

const Post = (props) => {
  const [photo, setPhoto] = useState(null);
  const [text, setText] = useState("");
  const [loading, setLoading] = useState(false);

  const handleClick = async () => {
    if (photo && text) {
      setLoading(true);
      const result = await fetchingUploadProfile(photo, "post");
      const url = result.data.secure_url;
      const time = new Date().getTime();
      props.fetchingPost(props.user._id, text, time, url);
    }
  };
  const onDrop = (picture) => {
    if (picture.length === 0) {
      return;
    } else {
      setPhoto(picture[0]);
    }
  };

  useEffect(() => {
    if (props.post._id) {
      setLoading(false);
      props.fetchingGetPostList();
      window.location.reload();
    }

    return () => {
      props.fetchingClearPost();
    };
  }, [props.post._id]);

  useEffect(() => {
    props.fetchingHeaderName("post");
    window.scroll(0, 0);
  }, []);
  return (
    <div className="upload-wrapper">
      <div className="upload-content">
        <div className="upload-text">
          <textarea
            placeholder="write down what you would like to share..."
            value={text}
            onChange={(e) => setText(e.target.value)}
          />
        </div>
        <div className="upload-image">
          <ImageUploader
            className="uploader"
            withIcon={true}
            onChange={onDrop}
            imgExtension={[".jpg", ".gif", ".png", ".gif", ".jpeg"]}
            maxFileSize={10000000}
            singleImage={true}
            withPreview={true}
            buttonText="Choose a image"
          />
        </div>
      </div>
      <div
        className="confirm"
        onClick={handleClick}
        style={{
          right: `${(props.innerWidth - props.viewWidth) / 2 + 15}px`,
        }}
      >
        confirm
      </div>
      {loading ? <Loading /> : null}
    </div>
  );
};

export default connect(
  (state) => ({
    post: state.post,
    user: state.user,
    viewWidth: state.viewWidth,
    innerWidth: state.innerWidth,
  }),
  {
    fetchingHeaderName,
    fetchingPost,
    fetchingClearPost,
    fetchingGetPostList,
  }
)(withRouter(Post));
