import React, { useEffect } from "react";
import { connect } from "react-redux";
import Profile from "../../component/profile/profile";
import {
  fetchingHeaderName,
  fetchingUpdateNewCommentReadSetting,
  fetchingGetCurrentUserFullPost,
  fetchingClearUnreadNum,
} from "../../redux/actions/actions";
import "./newComment.css";

const NewComment = (props) => {
  useEffect(() => {
    props.fetchingHeaderName("New Comment");
  }, [props.headerName]);

  useEffect(() => {
    return () => {
      props.fetchingClearUnreadNum("comment");
    };
  }, []);

  useEffect(() => {
    if (props.newCommentList.length !== 0) {
      props.newCommentList.map((v) => {
        return props.fetchingUpdateNewCommentReadSetting(v._id, props.user._id);
      });
    }
  }, [props.newCommentList.length]);

  return (
    <div className="newComment-wrapper">
      {props.newCommentList.map((v, index) => (
        <Profile
          key={index}
          userId={props.user._id}
          text={v.text}
          time={v.time}
          image={v.image}
          username={props.user.username}
          age={props.user.age}
          head={props.user.head}
          prefer={props.user.prefer}
          sex={props.user.sex}
          postId={v._id}
          value_postLikeList={v.postLikeList}
          value_postCommentList={v.comment}
          disable={true}
        />
      ))}
    </div>
  );
};

export default connect(
  (state) => ({
    user: state.user,
    currentUserFullPost: state.currentUserFullPost,
    newCommentList: state.newCommentList,
    newCommentReadSettingResult: state.newCommentReadSettingResult,
    headerName: state.headerName,
  }),
  {
    fetchingHeaderName,
    fetchingUpdateNewCommentReadSetting,
    fetchingGetCurrentUserFullPost,
    fetchingClearUnreadNum,
  }
)(NewComment);
