import React from "react";
import "./loading.css";
import { connect } from "react-redux";

const Loading = (props) => {
  return (
    <div
      className="loading"
      style={{
        left: `${(props.innerWidth - props.viewWidth) / 2}px`,
        width: `${props.viewWidth}px`,
      }}
    >
      <img src={require("../../assets/imges/loading.gif")} alt="loading" />
    </div>
  );
};

export default connect((state) => ({
  viewWidth: state.viewWidth,
  innerWidth: state.innerWidth,
}))(Loading);
